import {
  appendHeader,
  assignAdminNavbar,
  calculateAgeFromDOB,
  correctNavbarHref,
  fetchUrl,
  formatCurrency,
  formatTimestampToAMPM,
  getDateFromTimestamp,
  getToken,
  logoutBtnOnClick,
  redirectTo,
  removeOptions,
} from "../../util";
import { ROLES, monthsArr } from "../../util/constants";
import {
  XANO_ADMIN_GYMS_URL,
  XANO_ADMIN_SESSIONS_SEARCH_URL,
  XANO_ADMIN_USERS_SEARCH_URL,
  XANO_ADMIN_SIGN_IN_REDIRECT,
} from "../../util/xano_urls";
import { initSearch } from "../../util/search";

(() => {
  let UrlGymId, selectedFinanceMonth;

  window.onload = async function () {
    logoutBtnOnClick(XANO_ADMIN_SIGN_IN_REDIRECT, "sidebar-logout-btn");


    var curUrl = new URL(document.location.href);
    UrlGymId = curUrl.searchParams.get("gymCode") || null;

    if (UrlGymId) correctNavbarHref(UrlGymId);
    else {
      // ? TODO: Change this to a modal showcasing the error
      console.error("No gym id found");
      return;
    }

    const token = getToken();
    if (token) {
      const result = await fetchUrl(XANO_ADMIN_GYMS_URL + UrlGymId, "GET", false, token);

      if (result.isFetchOk !== undefined && !result.isFetchOk) {
        console.error(result.message);
        // redirectTo(`${SIGN_IN_URL}?reason=NOT_SIGNED_IN`);
        return;
      }

      const { team_role, ...data } = result;

      if (team_role && (team_role === ROLES.SUPER_ADMIN || team_role === ROLES.ADMIN)) {
        assignAdminNavbar(team_role);
        getSuperAdminData(data, token);
      } else {
        redirectTo(`${XANO_ADMIN_SIGN_IN_REDIRECT}?reason=UNAUTHORIZED_USER`);
      }
    } else {
      redirectTo(`${XANO_ADMIN_SIGN_IN_REDIRECT}?reason=NOT_SIGNED_IN`);
    }
  };

  async function getSuperAdminData(data, token) {
    const { sessions, users, gym, finances } = data;

    if (gym) appendHeader(gym);

    let indx;
    let isMonthFound = finances.some((month, i) => {
      if (month.month === new Date().getMonth() + 1) {
        indx = i;
        return true;
      } else false;
    });

    selectedFinanceMonth = isMonthFound ? finances[indx] : finances[finances.length - 1];
    if (selectedFinanceMonth === undefined) selectedFinanceMonth = null;
    appendStatistics(users, selectedFinanceMonth, sessions);
    initSelectMonth(users, selectedFinanceMonth, finances, sessions);

    appendSessions(sessions, gym.code);
    appendUsers(users);
    initSearch(
      { data: sessions, token, API: XANO_ADMIN_SESSIONS_SEARCH_URL, extraXanoInputObj: [{ gym_id: UrlGymId }] },
      appendSessions,
      gym.code
    );
    initSearch(
      {
        data: users,
        token,
        API: XANO_ADMIN_USERS_SEARCH_URL,
        searchContainerElId: "user-search-container",
        extraXanoInputObj: [{ gym_id: UrlGymId }],
      },
      appendUsers,
      gym.code
    );
  }

  function initSelectMonth(users, selectedFinanceMonth, finances, sessions) {
    let monthSelect = document.getElementById("select-month");

    removeOptions(monthSelect);
    finances.forEach((f, indx) => {
      var opt = document.createElement("option");
      opt.value = indx;
      opt.innerText = monthsArr[f.month - 1] + ", " + f.year;
      if (f.month === selectedFinanceMonth.month) opt.selected = true;
      monthSelect.appendChild(opt);
    });

    monthSelect.onchange = (e) => {
      selectedFinanceMonth = finances[e.target.value];
      if (selectedFinanceMonth === undefined) selectedFinanceMonth = null;
      appendStatistics(users, selectedFinanceMonth, sessions);
    };
  }

  function appendStatistics(users = [], finances = [], sessions = []) {
    if (!finances) {
      document.getElementById("gym-statistics-month-year").innerText = "This months data not found";
      document.getElementById("financial-expenses").innerText = "No data found";
      document.getElementById("financial-revenue").innerText = "No data found";
      document.getElementById("financial-profit").innerText = "No data found";
    } else {
      document.getElementById("gym-statistics-month-year").innerText =
        monthsArr[finances.month - 1] + " " + finances.year;

      document.getElementById("financial-expenses").innerText = formatCurrency(Number(finances.additional_expenses));
      document.getElementById("financial-revenue").innerText = formatCurrency(
        Number(finances.gross_revenue) + Number(finances.additional_revenue)
      );
      document.getElementById("financial-profit").innerText = formatCurrency(finances.net_profit);

      document.getElementById("gym-statistics-total-members").innerText = users?.length;

      const currentMonth = new Date().getMonth();
      const currentYear = new Date().getFullYear();

      const currentMonthSessions = sessions?.filter(
        (session) =>
          new Date(session.time).getMonth() === currentMonth &&
          new Date(session.time).getFullYear() === currentYear &&
          session.status === "COMPLETED"
      );
      $("#gym-statistics-completed-sessions").text(currentMonthSessions.length || 0);

      $("#gym-statistics-active-members").text(
        users.filter((user) => user.membership_status !== "INACTIVE" && user.membership_status !== "TRIAL").length
      );
    }
  }

  function appendSessions(data, code) {
    const sessionsContainer = document.getElementById("sessions-container");
    const og_webflow_element = sessionsContainer.querySelector("#table-item");

    function appendSessionsFn(_data) {
      let allItems = sessionsContainer.querySelectorAll(".adb-user.w-inline-block");
      allItems.forEach((item, indx) => {
        if (indx !== allItems.length - 1) item.remove();
      });

      _data.forEach((session, indx) => {
        const clonedCard = og_webflow_element.cloneNode(true); // true means clone all childNodes and all event handlers
        clonedCard.setAttribute("id", "");
        clonedCard.setAttribute("href", `/admin/session?sessionId=${session.id}`);
        let el = clonedCard.querySelectorAll(".colcontent");

        if (indx % 2 === 0) clonedCard.style.backgroundColor = "#00edf4";

        el[1].innerText = session.session_name;
        el[2].innerText = session.name || "Unknown";
        el[3].innerText = code;
        el[4].innerText = getDateFromTimestamp(session.time);
        el[5].innerText = formatTimestampToAMPM(session.time, true);
        el[6].innerText = session?.users?.length ?? 0;
        el[7].innerText = session.status;

        clonedCard.style.display = "flex";
        sessionsContainer.prepend(clonedCard);
      });
    }

    appendSessionsFn(data);
    og_webflow_element.style.display = "none";
  }

  function appendUsers(data) {
    const usersContainer = document.getElementById("users-container");
    const og_webflow_element = usersContainer.querySelector("#table-item");

    function appendUsersFn(_data) {
      let allItems = usersContainer.querySelectorAll(".adb-user.w-inline-block");
      allItems.forEach((item, indx) => {
        if (indx !== allItems.length - 1) item.remove();
      });

      _data.forEach((user, indx) => {
        const clonedCard = og_webflow_element.cloneNode(true); // true means clone all childNodes and all event handlers
        clonedCard.setAttribute("id", "");
        clonedCard.setAttribute("href", `/admin/user?userId=${user.id}`);

        if (indx % 2 === 0) clonedCard.style.backgroundColor = "#00edf4";
        let el = clonedCard.querySelectorAll(".colcontent");

        if (user.profile_picture) el[0].style.backgroundImage = `url(${user.profile_picture.url})`;

        el[1].innerText = user.name;
        el[2].innerText = user.membership_status || "INACTIVE";
        el[3].innerText = user.gender;
        el[4].innerText = calculateAgeFromDOB(user.date_of_birth);

        clonedCard.style.display = "flex";
        usersContainer.prepend(clonedCard);
      });
    }

    appendUsersFn(data);
    og_webflow_element.style.display = "none";
  }
})();
